import {
    createUser,
    deleteUser,
    // getUsers,
    fetchUsers,
    updatePassword,
    updatePasswordAdmin,
    updateUser,
    updateUserStatus,
    getUserGroups
} from "@/service/users";

const state = () => ({
    users: [],
    groups: [],
    pagination: {
        perPage: 20,
        page: 1,
        total: 0,
    }
});

const mutations = {
    SET_USERS(state, users) {
        state.users = users;
    },
    SET_PAGINATION(state, pagination) {
        state.pagination = pagination;
    }
};

const actions = {
    // async getUsers({ state }) {
    //     try {
    //         const data = await getUsers();
    //         state.users = data;
    //         return Promise.resolve();
    //     } catch (err) {
    //         Promise.reject;
    //     }
    // },
    async fetchUsers({ commit }, { perPage = 20, page = 1, keyword = "" }) {
        try {
            const users  = await fetchUsers(perPage, page, keyword);
            // console.log("Fetched Users from Store:", users);
    
            commit("SET_USERS", users.data);
            commit("SET_PAGINATION", {
                perPage: users.per_page,
                page: users.current_page,
                total: users.total,
            });
    
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },

    async createUser({ state }, payload) {
        try {
            const data = await createUser(payload);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async deleteUser({ state }, id) {
        try {
            const data = await deleteUser(id);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async updateUser({ state }, { id, payload }) {
        try {
            const data = await updateUser(id, payload);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async updateUserStatus({ state }, { id, payload }) {
        try {
            const data = await updateUserStatus(id, payload);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async updatePassword({ state }, payload) {
        return await updatePassword(payload);
    },
    async updatePasswordAdmin({ state }, payload) {
        return await updatePasswordAdmin(payload);
    },
    async getUserGroups({ state }) {
        try {
            if (state.groups.length == 0) {
                const { apiGroupList } = await getUserGroups();
                state.groups = apiGroupList;
            }
        } catch (err) {
            return Promise.reject();
        }
    }
};

const getters = {
    getUserById: (state) => (id) => {
        return state.users.find((item) => item.id === id);
    }
};

export default {
    namespaced: true,
    state,
    actions,
    getters,
    mutations
};
